import React, { useState } from 'react';
import { divFactory, noprop } from 'react-slash';

const [Page, Welcome, NextButton, CancelButton, ButtonLine] = divFactory('fixing-page', 'welcome', 'modal-next-button', 'modal-cancel-button', 'button-line');
const [ModalWindow, ModalHeader, ModalContent] = divFactory('modal-window --positioning-modal', 'modal-header', 'modal-content');
const [FixingButton, CenterLayer] = divFactory('fixing-button', 'page center-layer');

export default ({navigation, modelName="Teszt Model"}) => {
  const [isModal, showModal] = useState(false);
  const clickCancel = () => showModal(false);
  const clickToReality = () => navigation('reality');

  const InfoModal = () => ( 
    <ModalWindow>
      <ModalHeader>Sikerült pozícionálni a tárgyat?</ModalHeader>
      <ButtonLine>
        <CancelButton onClick={noprop(clickCancel)}>Újra</CancelButton>
        <NextButton onClick={noprop(clickToReality)}>Igen</NextButton>
      </ButtonLine>
    </ModalWindow>
  )

  return (
    <Page>
        <FixingButton onClick={()=>showModal(true)}>Rögzítés</FixingButton>
        { isModal && <CenterLayer><InfoModal /></CenterLayer>}        
    </Page>
  );
}
