import React from 'react';
import MainPage from './MainPage';
import InfoPage from './InfoPage';
import QrPage from './QrPage';
import Positioning from './Positioning';
import ArworksPage from '../../sources/ArworksPage';

export default ({path, navigation}) => {
  
  switch (path) {
    case 'main'     : return <MainPage navigation={navigation} />;
    case 'info'     : return <InfoPage navigation={navigation} />;
    case 'qrRead'   : return <QrPage navigation={navigation} />;
    case 'position' : return <Positioning navigation={navigation} />;
    case 'reality'  : return <ArworksPage navigation={navigation} />;
    default         : return <div className='page'>- - undefined page - -</div>;
  }
}