import { actionCreator, useReducerActions } from "react-slash";
import { pathsNumber, quickInfo } from '../data/demo';

const demoInitialState = {
  path: 'reality',
  quick: quickInfo.itemName,
  isQuick: false,
}

const [NAVIGATION, navigation] = actionCreator('navigation');
const [SHOW_QUICK, showQuick] = actionCreator('show-quick');
const [HIDE_QUICK, hideQuick] = actionCreator('hide-quick');

const demoActions = { navigation, showQuick, hideQuick};

const demoReducer = (state, {type, payload}) => {
  switch (type) {
    case NAVIGATION: return {
      ...state, 
      path: payload, 
      page: pathsNumber[payload] && pathsNumber[payload].num || 0 
    };
    case SHOW_QUICK: return {...state, quick: payload, showQuick: true};
    case HIDE_QUICK: return {...state, showQuick: false};
    default: return state;
  }
};

export default () => useReducerActions(demoReducer, demoInitialState, demoActions);