import React from 'react';
import { divFactory } from "react-slash";
import { GEOMS, makeShape, RAD, play3dContent } from '../threeDimension/Geometries';
import ThreeStudio from '../threeDimension/ThreeStudio';
import * as THREE from 'three';
import { rnd, irnd } from '../utils/randoms';
import ThreeArea, { playSection } from '../threeDimension/ThreeArea';

const fullSize = {width:'100vw', height:'100vh'};

const testContent = ({scene, camera}) => {
  camera.translateZ(-50);
  if (Math.random() > .9) {
    const opacity = 1;
    const colors = [irnd(0xFFFFFF), opacity, irnd(0xFFFFFF)];
    const shape = [GEOMS.box, GEOMS.dedocahedron, GEOMS.icosahedron, GEOMS.octahedron, GEOMS.plane, GEOMS.tetrahedron];
    const solid = makeShape(shape[irnd(shape.length)], 100, 100, 10 + irnd(100), ...colors);
    const dir = new THREE.Vector3();
    camera.getWorldDirection(dir);
    solid.position.copy(camera.position.clone().add(dir));
    solid.translateX(rnd(500) - 250);
    solid.translateY(rnd(500) - 250);
    solid.position.addScaledVector(dir, 8000);
    scene.add(solid);
  }        
};

const arworksSetup = ({scene, camera, root}) => {  
  const plane = makeShape(GEOMS.box, 500, 5, 500, 0x555555, 1 , 0x000000);
  const item = makeShape(GEOMS.box, 50, 50, 300, 0x888888, 1 , 0x000000);
  plane.rotateX(10 / RAD);
  plane.rotateY(15 / RAD);
  plane.rotateZ(20 / RAD);
  item.position.setY(25);
  plane.add(item);
  scene.add(plane)


  return playSection(() => {
    plane.rotateY(.1/RAD)
  }, {scene});    
}

export default () => <ThreeArea add3DContent={arworksSetup} style={fullSize}/>;